<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="1100px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <!--  -->
        <el-row>
          <el-descriptions :border="true" direction="vertical" :column="8">
            <el-descriptions-item label="" content-class-name="wid80">{{ info.materialCode }}</el-descriptions-item>
            <el-descriptions-item label="">{{ info.materialName }}</el-descriptions-item>
            <el-descriptions-item label="" content-class-name="minWid100">{{ info.materialSpec }}</el-descriptions-item>
            <el-descriptions-item content-class-name="wid80" label="">{{ info.materialAssessType_dictText }}</el-descriptions-item>
            <el-descriptions-item content-class-name="wid80" label="">{{ info.deliveryNumber }}</el-descriptions-item>
            <el-descriptions-item content-class-name="wid80" label="">{{ info.unitName }}</el-descriptions-item>
            <el-descriptions-item label="SKU" :content-class-name="form.useMinUnit ? '' : 'wid100'">
              <!-- SKUSKU -->
              <template v-if="form.useMinUnit">
                <el-select
                  v-model="form.minSkuUnitId"
                  filterable
                  clearable
                  placeholder="SKU"
                  style="width: 170px;"
                  @change="handleInfoMinSkuNameChange"
                >
                  <el-option
                    v-for="item in minSkuUnitPullList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  />
                </el-select>
              </template>
              <span v-else>{{ info.minSkuName }}</span>
            </el-descriptions-item>
            <!-- SKUSPQ1 -->
            <el-descriptions-item label="SPQ" :content-class-name="form.useMinUnit ? '' : 'wid80'">
              <!-- SKUSPQ -->
              <template v-if="form.useMinUnit">
                <el-input-number v-model="form.materialSpq" :min="0" :precision="3" style="width: 110px;" />
              </template>
              <span v-else>{{ info.materialSpq }}</span>
            </el-descriptions-item>
          </el-descriptions>
        </el-row>
        <divider></divider>
        <el-form ref="form" :model="form" :rules="rules" inline>
          <!-- Tip SKU -->
          <el-form-item label="SKU" prop="useMinUnit">
            <el-radio-group v-model="form.useMinUnit" style="width: 90px;" @change="handleUseMinUnitChange">
              <el-radio-button :label="true"></el-radio-button>
              <el-radio-button :label="false"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <template v-if="!form.useMinUnit">
            <!-- Tip  -->
            <el-form-item label="" prop="unitId">
              <el-select v-model="form.unitId" filterable clearable placeholder="" style="width: 178px;" @change="handleUnitChange">
                <el-option
                  v-for="item in unitList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <!-- Tip  -->
            <el-form-item v-if="form.unitId" prop="unitNumber">
              <span style="marginRight: 12px;"></span>
              <el-input-number
                v-model="form.unitNumber"
                :min="1"
                style="width: 178px;"
              />
              <span style="marginLeft: 12px;">{{ form.unitIdShow }}</span>
            </el-form-item>
          </template>
          <!--    SKU -->
          <el-button type="primary" :disabled="!baseValidate1" @click="handleUpdate"></el-button>
        </el-form>
        <el-row>
          <table-com
            :key="refreshKey"
            :ref="refName"
            :table-height-type="340"
            :loading="loading"
            :index="true"
            :index-fixed="true"
            :selection="false"
            :multiple="multiple"
            :selection-fixed="false"
            :table-column-sort="false"
            :table-data="tableData"
            :column-list="columnList"
            :show-operate="true"
            :operation-width="120"
            :operate-fixed="'right'"
            :table-selection="tableSelection"
            @refresh="refresh"
            @selectionChange="selectionChange"
          >
            <template slot-scope="scope">
              <el-button type="primary" :disabled="(numberSum < info.deliveryNumber) || (numberSum > info.deliveryNumber)" @click="handlePrintBarcode(scope.row)"></el-button>
            </template>
          </table-com>
        </el-row>
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" :disabled="hasDuplicates" @click="confirmDialog"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import { v4 as uuidv4 } from 'uuid'
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import { getPullListItemApi as unitPullListApi } from '@/apis/modules/base/baseUnit'
import { getBottomDetailApi as getListApi, getBottomDetailListApi, addMaterialDetailCheckApi, submitBottomDetailListApi, addBottomDetailListApi, barcodePrintApi } from '@/apis/modules/in-storage/purchase/delivery'

const INIT_FORM = () => ({
  deliveryReceiptId: '', // 
  orderMaterialId: '', // 
  materialId: '', // 
  materialCode: '', // 
  materialName: '', // 
  materialSpec: '', // 
  unitId: '', // 
  unitIdShow: '', // ()
  minSkuName: '', //  SKU
  minSkuUnitId: '', //   SKUid
  materialSpq: '', //  SPQ
  useMinUnit: true, //  SKU()
  spq: '', // 
  deliveryNumber: '', //  
  unitMeasureId: '', //  
  unitName: '', //  
  unitNumber: 1 // 
})

export default {
  name: 'MaterialOrderSelect',
  mixins: [paginationPage, printTagWired],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // id  {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // ids
    selection: {
      type: Array,
      default: () => []
    },
    // 
    max: {
      type: Number,
      default: 0
    },
    dialogForm: {
      type: Object,
      default: () => {
        return {}
      }
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialOrderSelect',
      // ()
      info: {
        ...INIT_FORM()
      },
      // ()
      form: {
        ...INIT_FORM()
      },
      rules: {},
      tableData: [],
      columnList: [],
      columnListBase: [
        {
          module: 'input',
          prop: 'batchNo',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          showOverflowTooltip: false
        },
        {
          module: 'number',
          prop: 'number',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 1
          },
          disabled: (scope) => {
            // Tip SKU"",
            return this.form.useMinUnit || this.tableData.length === 1
          },
          setPrecision: (row) => {
            return 3
          },
          handleChange: (row) => {
            // const sum = this.tableData.reduce((num, cur) => num + cur.number, 0)
            // console.log('444:', sum)
            // this.numberSum = sum
            // if (this.numberSum < this.info.deliveryNumber) {
            //   this.$notify.error({
            //     title: '',
            //     message: ``
            //   })
            // }
            // if (this.numberSum > this.info.deliveryNumber) {
            //   this.$notify.error({
            //     title: '',
            //     message: ``
            //   })
            // }
            this.handleNumberChange(row)
          },
          showOverflowTooltip: false
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // -
        {
          module: 'date',
          prop: 'productionDate',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          clearable: true,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            // return !item.expirationDate ? '' : ''
            return !item.productionDate ? '' : ''
          },
          handleChange: (row) => {
            this.handleCalculateDate(row, '')
          },
          showOverflowTooltip: false
        },
        {
          module: 'word',
          prop: 'barCode',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'expirationTime',
          label: '()',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: () => 1,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            return !item.expirationDate ? '' : ''
          },
          handleChange: (row) => {
            this.handleCalculateDate(row, '()')
          },
          showOverflowTooltip: false
        },
        {
          module: 'date',
          prop: 'qualityGuaranteePeriod',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          disabled: false,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            return !item.expirationDate ? '' : ''
          },
          showOverflowTooltip: false,
          handleChange: (row) => {
            this.handleCalculateDate(row, '')
          }
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnListExtra: [
        {
          module: 'input',
          prop: 'batchNo',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          showOverflowTooltip: false
        },
        {
          module: 'number',
          prop: 'number',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 1
          },
          disabled: (scope) => {
            // Tip SKU"",
            return this.form.useMinUnit || this.tableData.length === 1
          },
          setPrecision: (row) => {
            return 3
          },
          handleChange: (row) => {
            // const sum = this.tableData.reduce((num, cur) => num + cur.number, 0)
            // console.log('444:', sum)
            // this.numberSum = sum
            // if (this.numberSum < this.info.deliveryNumber) {
            //   this.$notify.error({
            //     title: '',
            //     message: ``
            //   })
            // }
            // if (this.numberSum > this.info.deliveryNumber) {
            //   this.$notify.error({
            //     title: '',
            //     message: ``
            //   })
            // }
            this.handleNumberChange(row)
          },
          showOverflowTooltip: false
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'date',
          prop: 'productionDate',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          clearable: true,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            // return !item.expirationDate ? '' : ''
            return !item.productionDate ? '' : ''
          },
          handleChange: (row) => {
            this.handleCalculateDate(row, '')
          },
          showOverflowTooltip: false
        },
        {
          module: 'word',
          prop: 'barCode',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'input',
          prop: 'snNo',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          clearable: true,
          showOverflowTooltip: false,
          handleBlur: (item) => {
            // const arr = this.tableData.filter(c => c.snNo === item.snNo)
            // if (arr.length > 1) {
            //   this.$notify.warning({
            //     title: '',
            //     message: ''
            //   })
            // }
            // 
            if (this.hasDuplicates) {
              this.$notify.warning({
                title: '',
                message: ``
              })
              return
            }
          }
        },
        {
          module: 'number',
          prop: 'expirationTime',
          label: '()',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: () => 1,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            return !item.expirationDate ? '' : ''
          },
          handleChange: (row) => {
            this.handleCalculateDate(row, '()')
          },
          showOverflowTooltip: false
        },
        {
          module: 'date',
          prop: 'qualityGuaranteePeriod',
          label: '',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          disabled: true,
          isShow: (item) => {
            return item.expirationDate // 
          },
          getDisplay: (item) => {
            return !item.expirationDate ? '' : ''
          },
          showOverflowTooltip: false,
          handleChange: (row) => {
            this.handleCalculateDate(row, '')
          }
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      unitList: [],
      minSkuUnitPullList: [],
      numberSum: 0,
      refreshKey: 0
    }
  },
  computed: {
    // SKU
    isSnNo() {
      const minSkuUnit = this.minSkuUnitPullList.find(c => c.id === this.form.minSkuUnitId)
      return minSkuUnit && minSkuUnit.name === ''
    },
    // 
    hasDuplicates() {
      const snNoArr = this.tableData.filter(c => c.snNo).map(c => c.snNo)
      return new Set(snNoArr).size !== snNoArr.length
    },
    baseValidate1() {
      let bool = true
      // 
      if (this.form.useMinUnit === '') {
        bool = false
        return bool
      }
      if (this.form.useMinUnit === false) {
        if (!this.form.unitId) {
          bool = false
        } else {
          if (!this.form.unitNumber) {
            bool = false
          }
        }
      } else {
        if (this.form.minSkuUnitId) {
          if (!this.form.materialSpq) {
            bool = false
          }
        } else {
          bool = false
        }
      }
      return bool
    }
  },
  watch: {
  },
  methods: {
    // Tip 
    initSelect() {
      this.getUnitPullList('2')
      this.getUnitPullList('3')
      this.init()
      this.initList()
    },
    // Tip   
    init() {
      const params = {
        ...this.extraInfo
      }
      // 
      // 
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const d = { ...data, unitNumber: !data.unitNumber || 1, unitId: data.unitId || '', unitIdShow: '', /**  */ materialSpq: (!data.materialSpq || data.materialSpq <= 0) ? 1 : data.materialSpq }

            this.info = _.cloneDeep(d)
            this.numberSum = this.info.deliveryNumber // =
            this.form = _.cloneDeep(d)
            // SKU
            if (!this.form.useMinUnit) {
              // 
              if (this.form.unitId) {
                const unitInfo = this.unitList.find(c => c.id === this.form.unitId)
                this.form.unitIdShow = unitInfo ? unitInfo.name : ''
              } else {
                this.form.unitIdShow = ''
              }
            } else {
              // SKUSPQ1
              // if (this.isSnNo) {
              //   this.form.materialSpq = 1
              // } else {
              //   this.form.materialSpq = ''
              // }
              // SKUSKUSPQ1
              this.form.materialSpq = 1
            }
            this.resetTableColumn()
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 
    initList() {
      this.loading = true
      // 
      return new Promise((resolve, reject) => {
        getBottomDetailListApi({
          page: {
            ...this.pageInfo
          },
          ...this.extraInfo
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // 
            this.tableData = data.map(c => ({ ...c, expirationTime: 1 }))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    getUnitPullList(type) {
      unitPullListApi({ type })
        .then(res => {
          const { code, data } = res
          if (!code) {
            type === '2' && (this.minSkuUnitPullList = data) // SKU
            type === '3' && (this.unitList = data) // 
          }
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // Tip SKU
    handleInfoMinSkuNameChange() {
      // if (this.isSnNo) {
      //   this.form.materialSpq = 1
      // } else {
      //   this.form.materialSpq = ''
      // }
      // SKUSKUSPQ1
      this.form.materialSpq = 1
      this.resetTableColumn()
    },
    // Tip SKU
    handleUseMinUnitChange(type) {
      this.form.minSkuUnitId = this.info.minSkuUnitId
      this.form.materialSpq = this.info.materialSpq
      this.form.unitId = ''
      this.form.unitNumber = 1
      this.form.unitIdShow = ''
      this.tableData = new Array(0)
      this.resetTableColumn()
    },
    // Tip 
    resetTableColumn() {
      if (this.form.useMinUnit && this.isSnNo) {
        this.columnList = [...this.columnListExtra]
      } else {
        this.columnList = [...this.columnListBase]
      }
    },
    // Tip 
    handleUnitChange(unitId) {
      this.form.unitNumber = 1
      const info = this.unitList.find(i => i.id === unitId)
      if (info) {
        const { name } = info
        this.$set(this.form, 'unitIdShow', name)
      } else {
        this.$set(this.form, 'unitIdShow', '')
      }
    },
    baseValidate() {
      let bool = true
      if (!this.form.useMinUnit) {
        if (!this.form.unitId) {
          this.$notify.error({
            title: '',
            message: ``
          })
          bool = false
        } else {
          if (!this.form.unitNumber) {
            this.$notify.error({
              title: '',
              message: `${this.form.unitIdShow}0`
            })
            bool = false
          }
        }
      } else {
        if (this.form.minSkuUnitId) {
          if (!this.form.materialSpq) {
            this.$notify.error({
              title: '',
              message: `SPQ`
            })
            bool = false
          }
        } else {
          this.$notify.error({
            title: '',
            message: `SKU`
          })
          bool = false
        }
      }
      return bool
    },
    // Tip 
    handleUpdate() {
      if (!this.baseValidate()) return
      this.loading = true
      const params = {
        ...this.extraInfo, // deliveryMaterialId ID
        materialId: this.form.materialId, // id
        useMinSku: this.form.useMinUnit, // SKU
        deliveryNumber: this.info.deliveryNumber, // 
        // skuperBarcodeNumspq
        // unitNumber
        ...this.form.useMinUnit && { perBarcodeNum: this.form.materialSpq },
        ...!this.form.useMinUnit && { unitNumber: this.form.unitNumber },
        unitId: this.form.useMinUnit ? this.form.minSkuUnitId : this.form.unitId
      }
      addBottomDetailListApi(params)
        .then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.map(c => ({ ...c, id: uuidv4(), printNumber: 0, expirationTime: 1 }))
            this.tableData.forEach(c => {
              this.form.useMinUnit && this.isSnNo && c.productionDate && this.handleCalculateDate(c, '')
            })
          }
          this.loading = false
        }).catch(err => {
          console.log(err)
          this.loading = false
        })
    },
    // Tip 
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // Tip 
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // Tip  
    confirmDialog() {
      if (!this.baseValidate()) return
      if (!this.tableData.length) {
        this.$notify.error({
          title: '',
          message: ``
        })
        return
      }
      if (this.numberSum < this.info.deliveryNumber) {
        this.$notify.error({
          title: '',
          message: ``
        })
        return
      }
      if (this.numberSum > this.info.deliveryNumber) {
        this.$notify.error({
          title: '',
          message: ``
        })
        return
      }
      // 
      const absenceBatchNo = this.tableData.some(({ id, ...info }) => !info.batchNo)
      if (absenceBatchNo) {
        this.$notify.error({
          title: '',
          message: ``
        })
        return
      }
      // 
      const absenceTime = this.tableData.some(({ id, ...info }) => info.expirationDate && !info.qualityGuaranteePeriod)
      if (absenceTime) {
        this.$notify.error({
          title: '',
          message: ``
        })
        return
      }
      const absenceSnNOArr = this.tableData.filter(c => !c.snNo)
      if (this.form.useMinUnit && this.isSnNo) {
        // 
        if (absenceSnNOArr.length === 1) {
          const ind = this.tableData.findIndex(c => !c.snNo)
          this.$notify.error({
            title: '',
            message: `${ind + 1}`
          })
          return
        }
        if (absenceSnNOArr.length > 1) {
          this.$notify.error({
            title: '',
            message: ``
          })
          return
        }
        // 
        if (this.hasDuplicates) {
          this.$notify.warning({
            title: '',
            message: ``
          })
          return
        }
      }
      const params = {
        ...this.extraInfo, // deliveryMaterialId ID
        dmdList: this.tableData.map(({ id, ...info }) => info)
      }
      const baseParams = {
        materialId: this.form.materialId,
        minSkuUnitId: this.form.minSkuUnitId,
        spq: this.form.materialSpq,
        useMinSku: this.form.useMinUnit, // SKU
        unitId: this.form.useMinUnit ? this.form.minSkuUnitId : this.form.unitId,
        confirmFlag: false,
        ...params // id
      }
      if (!this.form.useMinUnit) {
        this.submitBottomDetailList({ ...params, ...baseParams }) // sku
      } else {
        // 
        addMaterialDetailCheckApi({ materialId: this.form.materialId, minSkuUnitId: this.form.minSkuUnitId, spq: this.form.materialSpq })
          .then(res => {
            const { code, data } = res
            if (!code) {
              if (!data) {
                this.submitBottomDetailList(baseParams)
              } else {
                this.confirmEditMaterial(baseParams)
              }
              return
            }
          })
      }
    },
    // 
    confirmEditMaterial(params) {
      this.$confirm(`SQPSKU?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        cancelButtonClass: 'cancel-button-class',
        confirmButtonClass: 'confirm-button-class',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        this.submitBottomDetailList({ ...params, confirmFlag: true })
      }).catch(() => {
        this.submitBottomDetailList({ ...params })
      })
    },
    // 
    submitBottomDetailList(params) {
      // 
      this.loading = true
      submitBottomDetailListApi(params)
        .then(res => {
          const { code } = res
          if (!code) {
            this.$emit('confirm')
            this.beforeClose()
          }
          this.loading = false
        }).catch(err => {
          console.log(err)
          this.loading = false
        })
    },
    // Tip 
    handlePrintBarcode(item) {
      console.log('barCodeType-----', item)
      // 1
      item.printNumber++
      this.getDataAndPrint(barcodePrintApi, {
        dmdEntity: {
          deliveryMaterialId: item.deliveryMaterialId || '', // id
          materialId: item.materialId || '', // id
          batchNo: item.batchNo || '', // 
          number: item.number || 0, // 
          unitMeasureId: item.unitMeasureId || '', // id
          qualityGuaranteePeriod: item.qualityGuaranteePeriod || '', // 
          barCode: item.barCode || '', // 
          snNo: item.snNo || '', // 
          barCodeType: item.barCodeType || '' // 
        }
      })
        .then(res => {
          this.$emit('confirm')
          console.log('res', res)
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    refresh() {
    },
    handleNumberChange(row) {
      const curInd = this.tableData.findIndex(c => c.id === row.id)
      const firstRow = this.tableData[0]
      const lastRow = this.tableData[this.tableData.length - 1]
      const lastInd = this.tableData.findIndex(c => c.id === lastRow.id)
      console.log('@@@:', firstRow, lastRow, lastInd)
      this.$nextTick(() => {
        // 
        if (row.id !== lastRow.id) {
          // 
          this.$set(this.tableData, lastInd, {
            ...lastRow,
            number: this.info.deliveryNumber - this.tableData.reduce((num, cur) => num + (cur.id !== lastRow.id ? cur.number : 0), 0)
          })
          // 1
          if (this.tableData[lastInd].number < 1) {
            console.log('1')
            // 1
            this.$set(this.tableData, lastInd, {
              ...lastRow,
              number: 1
            })
            // 
            this.$set(this.tableData, curInd, {
              ...row,
              number: this.info.deliveryNumber - this.tableData.reduce((num, cur) => num + (cur.id !== row.id ? cur.number : 0), 0)
            })
            console.log('', this.tableData[curInd].number)
            this.$message({
              message: '',
              type: 'warning'
            })
          }
        } else { // 
          this.$set(this.tableData, 0, {
            ...firstRow,
            number: this.info.deliveryNumber - this.tableData.reduce((num, cur) => num + (cur.id !== firstRow.id ? cur.number : 0), 0)
          })
          if (this.tableData[0].number < 1) {
            this.$set(this.tableData, 0, {
              ...firstRow,
              number: 1
            })
            this.$set(this.tableData, curInd, {
              ...row,
              number: this.info.deliveryNumber - this.tableData.reduce((num, cur) => num + (cur.id !== row.id ? cur.number : 0), 0)
            })
            this.$message({
              message: '',
              type: 'warning'
            })
          }
        }
        this.numberSum = this.tableData.reduce((num, cur) => num + cur.number, 0)
        // this.refreshKey++
      })
    },
    handleCalculateDate(row, type) {
      // expirationTime 
      // productionDate 
      // qualityGuaranteePeriod 
      const checkDates = row => !(row.productionDate || row.qualityGuaranteePeriod) ? 0 : /*  */1
      const result = checkDates(row)
      if (result === 0) {
        // 
        return
      } else {
        // 
        if (row.productionDate && row.qualityGuaranteePeriod) {
          if (type === '' || type === '()') {
            const a = new Date(row.productionDate).getTime()
            const b = row.expirationTime * 24 * 60 * 60 * 1000
            const date = new Date(a + b)
            const formatObj = {
              y: date.getFullYear(),
              m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
              d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
            }
            row.qualityGuaranteePeriod = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
          } else if (type === '') {
            const a = new Date(row.qualityGuaranteePeriod).getTime()
            const b = row.expirationTime * 24 * 60 * 60 * 1000
            const date = new Date(a - b)
            const formatObj = {
              y: date.getFullYear(),
              m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
              d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
            }
            row.productionDate = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
          }
          return
        }

        if (row.productionDate) { // 
          const a = new Date(row.productionDate).getTime()
          const b = row.expirationTime * 24 * 60 * 60 * 1000
          const date = new Date(a + b)
          const formatObj = {
            y: date.getFullYear(),
            m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
            d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
          }
          row.qualityGuaranteePeriod = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
          return
        }

        if (row.qualityGuaranteePeriod) { // 
          const a = new Date(row.qualityGuaranteePeriod).getTime()
          const b = row.expirationTime * 24 * 60 * 60 * 1000
          const date = new Date(a - b)
          const formatObj = {
            y: date.getFullYear(),
            m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
            d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
          }
          row.productionDate = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
        }
        return
      }
    }
  }
}
</script>

