<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="75%"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div style="margin: 0 10px;">
      <divider></divider>
      <el-row>
        <el-descriptions :border="true" direction="vertical" :column="8">
          <el-descriptions-item label="">{{ dialogForm.materialCode }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.materialName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.materialSpec }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.materialAssessType_dictText }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.orderNumber }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.unitName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.deliveredNumber }}</el-descriptions-item>
          <el-descriptions-item label="">{{ dialogForm.deliveryNumber }}</el-descriptions-item>
        </el-descriptions>
      </el-row>

      <!-- <el-row> -->
      <divider></divider>
      <el-row style="margin: 10px 0;">
        <!-- isReverse true() -->
        <template v-if="$route.params.view !== 'detail' && !extraInfo.isReverse">
          <el-button type="warning" @click="handleModify"></el-button>
          <el-button type="danger" @click="handleClean"></el-button>
        </template>
        <el-button type="primary" :disabled="tableData.length === 0" @click="handlePrintAll"></el-button>
      </el-row>
      <table-com
        :key="refreshKey"
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :table-height-type="380"
        :operation-width="120"
        :expand-list="expandList"
        :table-column-sort="false"
        :show-operate="true"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handlePrint([scope.row.id])"></el-button>
        </template>
      </table-com>
    </div>
    <package-to-s-k-u
      v-model="skuVisible"
      :title="''"
      :extra-info="{columnList: columnList1 || [], tableData: dialogInfo1.subBarCodeList || []}"
      @print="handlePrint"
    />
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogInfo"
      :edit-type="editInfoType"
      :title="dialogTitle"
      :extra-info="{deliveryMaterialId: dialogForm.id}"
      @confirm="confirmCallBack"
    />
  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import EditDialog from './DetailDialog'
import PackageToSKU from '@/components/ModalSelect/PackageToSKU'
import { getBottomDetailListApi as getListApi, cleanBottomDetailListApi, printApi } from '@/apis/modules/in-storage/purchase/delivery'

export default {
  name: 'InStorageConsignCom',
  components: {
    EditDialog,
    PackageToSKU
  },
  mixins: [paginationPage, printTagWired],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => ({})
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: '',
      refreshKey: new Date().getTime(),
      loading: false,
      rules: {},
      query: {},
      tableData: [],
      columnList: [],
      columnList1: [],
      columnListBase: [
        {
          module: 'word',
          prop: 'batchNo',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'qualityGuaranteePeriod',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (item) => {
            // Tip expirationDate  0/1
            return !item.expirationDate ? '' : item.qualityGuaranteePeriod
          },
          headerAlign: 'center'
        },
        {
          module: 'link',
          type: (item) => Array.isArray(item.subBarCodeList) && !!item.subBarCodeList.length ? 'primary' : '',
          prop: 'barCode',
          underline: false,
          minWidth: 150,
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (item) => {
            if (!item.subBarCodeList.length) return
            this.skuVisible = true
            this.dialogInfo1 = item
          }
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'subBarCodeCount',
          label: 'SKU',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnListExtra: [
        {
          module: 'word',
          prop: 'batchNo',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'qualityGuaranteePeriod',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (item) => {
            // Tip expirationDate  0/1
            return !item.expirationDate ? '' : item.qualityGuaranteePeriod
          },
          headerAlign: 'center'
        },
        {
          module: 'link',
          type: (item) => Array.isArray(item.subBarCodeList) && !!item.subBarCodeList.length ? 'primary' : '',
          prop: 'barCode',
          underline: false,
          minWidth: 150,
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (item) => {
            if (!item.subBarCodeList.length) return
            this.skuVisible = true
            this.dialogInfo1 = item
          }
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'subBarCodeCount',
          label: 'SKU',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      expandList: [],
      dialogVisible: false,
      skuVisible: false,
      dialogInfo: {},
      dialogInfo1: {},
      editInfoType: 'add'
    }
  },
  computed: {
    dialogTitle() {
      return this.editType === 'add' ? `${this.title}` : `${this.title}`
    }
  },
  watch: {
    dialogForm: {
      handler(item) {
        this.$nextTick(() => {
          this.columnList = (item.useMinUnit && item.unitName2 === '') ? this.columnListExtra : this.columnListBase
          this.columnList1 = this.columnList.filter(c => c.prop !== 'subBarCodeCount')
        })
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...this.query,
          deliveryMaterialId: this.dialogForm.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    confirmCallBack() {
      this.init()
      // 
      this.$emit('confirm')
    },
    // 
    handleClean() {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        cleanBottomDetailListApi({
          deliveryMaterialId: this.dialogForm.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    // 
    handlePrintAll() {
      const ids = this.tableData.map(i => i.id)
      this.handlePrint(ids)
    },
    // 
    handlePrint(ids) {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      return new Promise((resolve, reject) => {
        printApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.device && data.length && this.$notify.success({
              title: '',
              message: data.length > 30 ? `` : ``
            })
            this.device && data.length && data.forEach(item => this.print(item))
            this.init()
              .then(res => {
                this.dialogInfo1 = this.tableData.find(c => c.id === this.dialogInfo1.id) || {}
              })
          }
          loading.close()
          resolve(res)
        }).catch(err => {
          loading.close()
          reject(err)
        })
      })
    },
    // 
    handleDelete(item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.tableData = this.tableData.filter(i => i.uuid !== item.uuid)
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    handleAdd() {
      this.editInfoType = 'add'
      this.dialogInfo = {}
      this.dialogVisible = true
    },
    // 
    handleModify() {
      this.dialogInfo = { ...this.dialogForm }
      this.editInfoType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.$nextTick(() => {
        this.columnList = (this.dialogForm.useMinUnit && this.dialogForm.unitName2 === '') ? this.columnListExtra : this.columnListBase
        this.columnList1 = this.columnList.filter(c => c.prop !== 'subBarCodeCount')
        this.init()
          .then(res => {
          // 
            this.$emit('confirm')
          })
      })
    },
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  ::v-deep .el-drawer__header {
    margin-bottom: 10px !important;
  }

  .drawer-footer{
    margin-top: 10px;
    margin-right: 30px;
  }
  </style>

