<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="1000px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <el-form ref="filterItem" class="top-filter clearfix" :model="query" inline @submit.native.prevent>
          <el-form-item prop="code">
            <el-input v-model.trim="query.code" placeholder="" class="filter-item" style="width: 178px;" clearable @keyup.enter.native="searchInit" />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          ></el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          ></el-button>
          <el-button v-if="!isCheckAll" class="filter-item" type="warning" plain @click="checkAll"></el-button>
          <el-button v-if="isCheckAll" class="filter-item" type="warning" plain @click="reverseSelection"></el-button>
        </el-form>
        <el-row>
          <table-com
            :ref="refName"
            :row-key="rowKey"
            :table-height-type="490"
            :loading="loading"
            :index="true"
            :index-fixed="false"
            :selection="true"
            :multiple="multiple"
            :selection-fixed="false"
            :table-column-sort="false"
            :table-data="tableData"
            :column-list="columnList"
            :show-operate="false"
            :operate-fixed="false"
            v-bind="$attrs"
            :table-selection="tableSelection"
            :selectable="selectable"
            @selectionChange="selectionChange"
            @sortChange="sortChange"
            @dynamicRow="getDynamicRow"
          />
        </el-row>
        <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-row style="text-align: left;">
        <el-button type="primary" :disabled="!rowObj" @click="handleSelectUp"></el-button>
        <el-button type="primary" :disabled="!rowObj" @click="handleCancelUp"></el-button>
        <el-button type="primary" :disabled="!rowObj" @click="handleSelectDown"></el-button>
        <el-button type="primary" :disabled="!rowObj" @click="handleCancelDown"></el-button>
      </el-row>
      <el-button type="primary" @click="handleConfirm"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import { addMaterialPageListApi as getListApi, addBottomListApi } from '@/apis/modules/in-storage/purchase/delivery'

export default {
  name: 'MaterialSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // id  {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // ids
    selection: {
      type: Array,
      default: () => []
    },
    // 
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      query: {
        code: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'notArrivedNumber',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'deliveryNumber',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          disabled: (c) => {
            // 
            return c.status === 3
          },
          setMin: () => {
            return 0
          },
          setMax: (c) => {
            return (c.orderNumber - c.deliveredNumber) // -
          },
          handleChange: _.throttle((row) => {
            // Tip 
            this.tableSelection.forEach(c => {
              if (c.id === row.id) {
                c.deliveryNumber = row.deliveryNumber
              }
            })
          }, 1000),
          setPrecision: () => {
            return 3
          }
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      rowObj: null,
      rowKey: 'id',
      // 
      isCheckAll: false
    }
  },
  methods: {
    init() {
      const params = {
        ...this.query,
        ...this.extraInfo,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 
    checkAll() {
      this.isCheckAll = true
      const params = {
        ...this.query,
        ...this.extraInfo,
        page: {
          current: 1, // 
          size: 10000 // 
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list } = data
            if (list && Array.isArray(list) && list.length) {
              // 
              this.tableSelection = list.filter(row => !this.selection.includes(row.id))
              if (!this.tableSelection.length) {
                this.isCheckAll = false
                this.$notify.warning({
                  title: '',
                  message: ``
                })
                return
              }

              // 
              const rowKey = this.rowKey
              const ids = this.tableSelection.map(i => i[rowKey]) // 
              const curPageCheckedArr = [] // de
              this.tableData.forEach(row => {
                function check(row) {
                  const id = row[rowKey]
                  return ids.some(i => i === id)
                }
                if (check(row)) {
                  curPageCheckedArr.push(row)
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })

              // 
              this.tableSelection.forEach(row => {
                // ,
                const ids = curPageCheckedArr.map(i => i[rowKey])
                if (!ids.includes(row[rowKey])) {
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    reverseSelection() {
      this.isCheckAll = false
      this.$nextTick(() => {
        this.$refs[this.refName].$refs.multipleTable.clearSelection()
        this.tableSelection = []
      })
    },
    // 
    sortChange({ column, prop, order }) {
      this.init()
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 
    initSelect() {
      this.searchInit()
    },
    // 
    selectionChange(arr) {
      this.tableSelection = [...arr]
    },
    //  Checkbox 
    getDynamicRow(item) {
      this.rowObj = item
    },
    // 
    handleSelectUp() {
      // 
      const operateItemIndex = this.tableData.findIndex(c => c.id === this.rowObj.id)
      if (operateItemIndex !== -1) {
        this.tableData.forEach((c, i) => {
          this.$nextTick(() => {
            i < operateItemIndex && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, true)
          })
        })
      }
    },
    // 
    handleCancelUp() {
      // 
      const operateItemIndex = this.tableData.findIndex(c => c.id === this.rowObj.id)
      if (operateItemIndex !== -1) {
        this.tableData.forEach((c, i) => {
          this.$nextTick(() => {
            i < operateItemIndex && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, false)
          })
        })
      }
    },
    // 
    handleSelectDown() {
      // 
      const operateItemIndex = this.tableData.findIndex(c => c.id === this.rowObj.id)
      if (operateItemIndex !== -1) {
        this.tableData.forEach((c, i) => {
          this.$nextTick(() => {
            i > operateItemIndex && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, true)
          })
        })
      }
    },
    // 
    handleCancelDown() {
      // 
      const operateItemIndex = this.tableData.findIndex(c => c.id === this.rowObj.id)
      if (operateItemIndex !== -1) {
        this.tableData.forEach((c, i) => {
          this.$nextTick(() => {
            i > operateItemIndex && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, false)
          })
        })
      }
    },
    // 
    beforeClose(done) {
      this.$emit('input', false)
      this.tableSelection = new Array(0)
      this.rowObj = null
      this.resetForm('filterItem')
      typeof done === 'function' && done()
    },
    // 
    handleConfirm() {
      const params = {
        dmList: this.tableSelection.map(c => ({
          orderMaterialId: c.orderMaterialId, // idid
          deliveryReceiptId: this.extraInfo.deliveryReceiptId, // id
          materialId: c.materialId, // id
          orderNumber: c.orderNumber, // 
          deliveryNumber: c.deliveryNumber // 
        }))
      }
      return new Promise((resolve, reject) => {
        addBottomListApi({
          ...params
        })
          .then(res => {
            const { code } = res
            if (!code) {
              this.$emit('confirm', this.tableSelection)
              this.beforeClose()
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    },
    // Tip 
    selectable(row, index) {
      // this.selection 
      return !this.selection.includes(row.id) // 
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
.main-box {
  padding: 10px;
  box-sizing: border-box;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
