/*
*   
*		:
*   
*   import tableHeadSet from '@/mixins/tableHeadSet'
*   :
*   mixins: [tableHeadSet]
*/

export default {
  data() {
    return {
      formIdent: '', // 
      tableData: [], // 
      columnList: [], // 
      expandList: [], // 
      tableColumnSortData: [],
      labels: {}, // 
      refreshKey: 1 // el-tablekey
    }
  },
  methods: {
    /*
    * 
    *   columnSets
    *  apiHeadSets
    * isExtend 
    * isShow 
    */
    initTableHeadMixins(apiHeadSets = [], module = 'word', columnSetsName = 'columnSets', width = 100) {
      //  columnSets  apiHeadSets 
      const totalHeadSets = apiHeadSets.map(i => {
        return {
          // isExtend: i.extend,
          // isShow: i.show,
          // headerAlign: i.headAlign,
          // isDict: i.dict,
          // isForceShow: i.forceShow,
          // extend: i.extend,
          isParam: false,
          ...i,
          width: module === 'input' && !i.width ? width : i.width,
          module
        }
      }).map(item => {
        const c = this[columnSetsName].find(i => i.prop === item.prop)
        return c ? { ...item, ...c } : { ...item }
      })

      // 
      this.$nextTick(() => {
        if (module === 'word') {
          this.expandList = totalHeadSets.filter(f => f.extend && f.show)
        }
      })

      /*
      *   
      *   1. columnSetsapiapiHeadSets
      *   2. showextend
      */
      this.$nextTick(() => {
        this.refreshKey += 1
      })
      return totalHeadSets.filter(f => f.show && !f.extend)
    }
  }
}
