<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <!--  -->
      <el-row>
        <table-com
          :ref="refName"
          :table-height-type="490"
          :loading="loading"
          :index="true"
          :index-fixed="true"
          :selection="true"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="false"
          :operate-fixed="false"
          :table-selection="tableSelection"
          @selectionChange="selectionChange"
        />
      </el-row>
      <!-- <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" /> -->
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListApi as getListSelectApi } from '@/apis/modules/in-storage/purchase'

export default {
  name: 'MaterialOrderSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // id  {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // ids
    selection: {
      type: Array,
      default: () => []
    },
    // 
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    url: { //   
      type: String,
      default: '/enterWarehouse/WmsOrder/listMaterial'
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialOrderSelect',
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'price',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMaterial',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'deliveryNumber',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setMax: (row) => {
            return row.enterNumber
          },
          setPrecision: (row) => {
            return 3
          }
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  methods: {
    confirmDialog() {
      const valid = this.tableSelection.every((item) => item.deliveryNumber !== 0)
      if (!valid) {
        this.$notify.error({
          title: '',
          message: `0`
        })
        return
      }
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    init() {
      const params = {
        ...this.query,
        materialIdList: this.ids, // id
        ...this.extraInfo // Id Type
      }
      return new Promise((resolve, reject) => {
        getListSelectApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // const { list, ...pageInfo } = data
            // this.initPage(pageInfo)
            this.tableData = data.map(i => {
              return {
                deliveryNumber: 0,
                ...i
              }
            })
            // selection
            this.tableSelection = this.tableData.filter(i => this.selection.includes(i.id))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 
    handleUpdateTableSelected(type = '') {

    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
