export default {
  data() {
    return {
      device: null, // 
      BrowserPrint: window.BrowserPrint,
      zebraPrinter: null,
      printType: 'barCode', // 
      isPrinting: false // 
    }
  },
  mounted() {
    this.getDevice()
  },
  beforeDestroy() {
    this.zebraPrinter = window.BrowserPrint
    this.device = null
  },
  methods: {
    // Tip 
    getDevice() {
      const _this = this
      // const Zebra = window.Zebra // 
      // console.log('BrowserPrint', this.BrowserPrint)
      this.BrowserPrint.getDefaultDevice('printer', function(device) {
        console.log('__&&&__***__===', device)
        if (device) {
          _this.device = device
        }
      }, function(err) {
        console.log(err)
        // this.$message.info('')
      })
    },
    // Tip 
    getDataAndPrint(printApi = new Promise(), printParams = {}) {
      if (!printApi) {
        console.log('')
        return Promise.reject(new Error('error'))
      }
      this.isPrinting = true
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      return new Promise((resolve, reject) => {
        printApi(printParams).then(res => {
          const { code, data } = res
          if (!code) {
            data.forEach(item => {
              this.print(item)
            })
          }
          loading.close()
          resolve(res)
        }).catch(err => {
          console.log('', err)
          loading.close()
          reject(err)
        })
      })
    },
    // Tip 
    print(data) {
      this.isPrinting = true
      const _this = this
      this.device && this.device.send(data, function(success) {
        console.log('Sent to printer')
        _this.isPrinting = false
      }, function(error) {
        console.error('Error:' + error)
        _this.isPrinting = false
      })
    }
  }
}
