<template>
  <div v-loading="loading" class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="beforeClose"> </el-button>
      </div>
      <div class="fr">
        <el-button v-if="params.view !== 'detail'" type="primary" style="margin-right: 10px;" @click="handleSave">{{ params.view === 'add' ? '' : '' }}</el-button>
        <!--
        0: '',1: '',2: '',3: '',4: '',5: '',6: ''
      -->
        <el-button v-show="params.view === 'detail'" :disabled="!([3, 4].includes(form.status) && form.canStockInMatarialCount > 0)" type="primary" @click="handleInWarehouse"></el-button>
      </div>
    </el-row>
    <!-- Tip  -->
    <transition name="el-zoom-in-top">
      <el-row>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="left" :disabled="params.view === 'detail'">
          <el-row :gutter="20">
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="contractNo">
                <el-input
                  v-model.trim="form.contractNo"
                  placeholder=""
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="orderNo">
                <el-input
                  v-model.trim="form.orderNo"
                  placeholder=""
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col v-if="params.view !== 'add'" :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="no">
                <el-input
                  v-model.trim="form.no"
                  placeholder=""
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="supplierName">
                <el-input
                  v-model.trim="form.supplierName"
                  placeholder=""
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="materialsUnit">
                <el-input
                  v-model.trim="form.materialsUnit"
                  placeholder=""
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="deliveryDate">
                <el-date-picker
                  v-model="form.deliveryDate"
                  type="date"
                  style="width: 100%;"
                  value-format="yyyy-MM-dd"
                  placeholder=""
                  :clearable="false"
                  :editable="false"
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="inspect">
                <!-- () -->
                <el-radio-group v-model="form.inspect" :disabled="params.view === 'modify' && form.status === 6" style="width: 178px;" class="my-inspect-sty">
                  <el-radio-button :label="true"></el-radio-button>
                  <el-radio-button :label="false"></el-radio-button>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="remarks">
                <el-input v-model="form.remarks" type="textarea" :rows="1" placeholder="" />
              </el-form-item>
            </el-col>
            <!-- <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="" prop="warehouseId">
                <el-select
                  v-model="form.warehouseId"
                  filterable
                  clearable
                  style="width: 100%;"
                  placeholder=""
                >
                  <el-option
                    v-for="(item,index) in warehousePullList"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  />
                </el-select>
              </el-form-item>
            </el-col> -->
          </el-row>
        </el-form>
      </el-row>
    </transition>
    <!-- Tip  -->
    <el-divider content-position="left"></el-divider>
    <!-- Tip - -->
    <el-row v-if="params.view !== 'detail'">
      <!-- Tip  -->
      <el-button
        v-if="['2','3','4'].includes(form.orderType)"
        type="primary"
        @click="handleAddMaterial(form.orderType)"
      ></el-button>
      <!-- isReverse true() -->
      <el-button
        v-if="form.orderType === '1' && params.view === 'modify' && !form.isReverse"
        type="primary"
        :disabled="!enabled"
        @click="handleAddMaterial1"
      ></el-button>
      <!-- <el-button
        type="success"
        :disabled="!form.id"
      ></el-button>
      <el-button
        type="primary"
        :disabled="!form.id"
      ></el-button> -->
      <el-button
        type="primary"
        icon="el-icon-setting"
        @click="handleSetTable"
      ></el-button>
    </el-row>
    <el-row>
      <div class="clearfix top-filter">
        <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
          <el-form-item prop="code">
            <el-input v-model="query.code" placeholder="" clearable style="width: 178px;" @keyup.enter.native="searchInit" />
          </el-form-item>
          <el-button type="success" icon="el-icon-search" @click="init"></el-button>
          <el-button type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')"></el-button>
          <span v-if="!['detail','add'].includes(params.view)" style="text-align: right;">
            <el-button type="primary" @click="handleAllGenerate"></el-button>
            <el-button type="primary" @click="handleAllPrint"></el-button>
          </span>
        </el-form>
      </div>
    </el-row>
    <!-- Tip -() -->
    <el-row>
      <table-com
        :key="tableComKey"
        :ref="refTableName"
        :ref-name="'tableRef'"
        class="base-table"
        :table-height-type="450"
        :selection="params.view === 'add'"
        :selection-fixed="false"
        :table-selection="tableSelection"
        :loading="tableLoading"
        :index="true"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="170"
        :expand-list="expandList"
        :show-operate="params.view !== 'add'"
        :cell-style="handleCellStyle"
        :refresh-key="refreshKey"
        :selectable="selectable"
        @rowClick="handleRowClick"
        @refresh="searchInit"
        @selectionChange="selectionChange"
        @dynamicRow="getDynamicRow"
      >
        <template slot-scope="scope">
          <!--  -->
          <el-button v-if="params.view !== 'detail'" type="primary" :disabled="scope.row.orderType === 0 || scope.row.status === 3" @click="handleGenerateBarcode(scope.row)"></el-button>
          <el-button v-if="params.view === 'detail'" type="primary" @click="handleViewBarcode(scope.row)"></el-button>
          <!-- isReverse true() -->
          <el-button v-if="params.view !== 'detail' && !form.isReverse" type="danger" :disabled="scope.row.orderType === 0 || scope.row.status === 3 || scope.row.status === 6" @click="handleDelete(scope.row)"></el-button>
        </template>
      </table-com>
    </el-row>
    <pagination
      v-if="params.view === 'add'"
      :page.sync="pageInfo.current"
      :limit.sync="pageInfo.size"
      :total="total"
      @pagination="paginationStatusChange"
    />

    <!--  -->
    <generate-barcode
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :extra-info="{isReverse: form.isReverse}"
      @confirm="confirmCallBack"
    />

    <!-- ()  -->
    <order-material
      v-if="['2','4'].includes(form.orderType)"
      v-model="orderSelectInfo.visible"
      :ids="tableData.map(i => i.materialId)"
      :extra-info="{id: Number(params.orderId), type: '4'}"
      @confirm="onSelectConfirm"
    />

    <!-- ()  -->
    <base-material
      v-if="['3'].includes(form.orderType)"
      v-model="selectInfo.visible"
      :ids="tableData.map(i => i.materialId)"
      :extra-info="{}"
      :number-control="false"
      @confirm="onSelectConfirm"
    />

    <!-- ()  -->
    <material-select
      v-model="visible"
      :extra-info="{orderId: params.orderId,deliveryReceiptId: params.id, excludeOrderMaterialIds: tableData.map(c => c.orderMaterialId)}"
      @confirm="handleChooseConfirm"
    />
  </div>
</template>

<script>
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import tableHeadSet from '@/mixins/tableHeadSet'
import GenerateBarcode from './Dialog'
// 
import OrderMaterial from '@/views/in-storage/purchase/OrderMaterial'
// (/)
import BaseMaterial from '@/views/in-storage/purchase/Material'
// 
import MaterialSelect from './MaterialChoose'
import { getListDetailApi as getDetailApi, getBottomListApi, addBottomListApi, delBottomListApi, editBottomListApi, addListApi, updateListApi, materialPrintApi, allPrintApi, allGenerateApi } from '@/apis/modules/in-storage/purchase/delivery'
import { getWarehouseSapPullListApi } from '@/apis/modules/base/basewmsWarehouse'
import moment from 'moment'
moment.locale('zh-cn')

const INIT_FORM = {
  no: '', // 
  contractNo: '', // 
  orderNo: '', // 
  supplierName: '', // 
  materialsUnit: '', // 
  deliveryDate: '', // 
  inspect: true, // 
  orderType: '', // 1234
  remarks: '', // 
  // warehouseId: '',
  status: null // 
}

export default {
  name: 'InStorageDeliveryAdd', // name
  components: {
    GenerateBarcode,
    OrderMaterial,
    BaseMaterial,
    MaterialSelect
  },
  mixins: [paginationPage, printTagWired, tableHeadSet],
  data() {
    return {
      title: '',
      loading: false,
      tableLoading: false, // loading
      query: {},
      tableData: [],
      tableSelection: [], // 
      // tableDefaultSelection: [], // 
      refreshKey: 0,
      tableComKey: 0,
      refTableName: 'baseTableRef',
      columnSets: [],
      expandList: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      params: {
        id: '', // id
        view: 'add'
      },
      form: INIT_FORM,
      rules: {
        deliveryDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        inspect: [
          { required: true, message: '', trigger: 'blur' }
        ]
        // warehouseId: [
        //   { required: true, message: '', trigger: 'change' }
        // ]
      },
      orderSelectInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      selected: new Set(),
      selectedRows: [],
      // warehousePullList: [],
      currentRow: null,
      visible: false,
      enabled: true
    }
  },
  computed: {
  },
  watch: {
  },
  mounted() {
    this.params = this.$route.params
    // this.getWarehousePullList()
    this.initBaseInfo() // (orderType)
    this.columnSets = [
      this.params.view === 'add' ? { // 
        module: 'number',
        prop: 'deliveryNumber',
        label: '',
        align: 'center',
        minWidth: 160,
        fixed: false,
        sortable: false,
        resizable: true,
        headerAlign: 'center',
        disabled: (row) => {
          // 
          if (this.params.view === 'detail' || row.status === 3) return true
          return false
        },
        setMin: (row) => {
          return 0
        },
        setMax: (c) => {
          if (this.form.orderType === '3') {
            // 
            return Infinity // 
          } else {
            //   
            return (c.orderNumber - c.deliveredNumber) // -
          }
        },
        handleChange: _.throttle((row) => {
          // Tip 
          if (this.selected.has(row.id)) {
            const ind = this.selectedRows.findIndex(c => c.id === row.id)
            this.selectedRows[ind] = row
          }
        }, 1000),
        setPrecision: (row) => {
          return 3
        }
      } : { // 
        module: 'word',
        prop: 'deliveryNumber',
        label: '',
        align: 'center',
        minWidth: 160,
        fixed: false,
        sortable: false,
        resizable: true,
        headerAlign: 'center'
      }
    ]
    this.listInit()
  },
  methods: {
    // Tip 
    initBaseInfo() {
      this.loading = true
      // Tip IDID
      const params = {
        ...this.params.view === 'add' && { orderId: this.params.orderId },
        ...this.params.view !== 'add' && { deliveryReceiptId: this.params.id }
      }
      // Tip 
      return new Promise((resolve, reject) => {
        getDetailApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            this.form = { ...data }
            // 
            this.form.deliveryDate = data.deliveryDate || moment().format('YYYY-MM-DD')
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip ()
    initAutoUpdate() {
      const params = {
        ...this.form
      }
      return new Promise((resolve, reject) => {
        updateListApi(params).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // Tip 
    handleSave() {
      this.$refs['form'].validate((valid) => {
        if (!valid) return
        const params = {
          saveType: this.params.view === 'add' ? 1 : 2, // add -   modify- 
          // 
          entity: {
            id: this.params.view === 'add' ? null : this.form.id,
            orderId: this.params.orderId, // id
            type: this.form.orderType, // --:1 2 3 4()
            contractNo: this.form.contractNo, // 
            deliveryDate: this.form.deliveryDate, // 
            inspect: this.form.inspect, // 
            remarks: this.form.remarks // 
            // warehouseId: this.form.warehouseId
          },
          // 
          // id: item.orderType === 1 ? item.id : null, // orderType === 1idnull
          // delete: false, // 
          dmList: this.params.view === 'add' ? this.selectedRows.map(c => ({ ...c, id: c.orderType === 1 ? c.id : null, delete: false })) : this.tableData.map(c => ({ ...c, id: c.orderType === 1 ? c.id : null, delete: false }))
        }
        if (!params.dmList.length) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
        this.loading = true
        addListApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            if (this.params.view === 'add') {
              // Tip id
              this.$set(this.form, 'id', data)
              // Tip 
              this.$store.dispatch('tagsView/delView', this.$route)
              // Tip 
              this.$router.push({
                name: 'InStorageDeliveryModify',
                params: {
                  view: 'modify',
                  orderId: this.params.orderId,
                  id: this.form.id,
                  type: this.form.orderType,
                  routeName: this.$route.name
                }
              })
            } else if (this.params.view === 'modify') {
              // Tip 
              this.$store.dispatch('tagsView/delView', this.$route)
              // Tip 
              const routeName = {
                1: 'InStoragePurchaseDelivery', // -
                2: 'InStorageAssistDelivery', // -
                3: 'InStorageConsignDelivery', // -
                4: 'InStorageFinishDelivery' // -
              }[`${this.form.orderType}`]
              routeName ? (this.$router.push({
                name: routeName,
                params: {
                  id: this.params.orderId,
                  type: this.form.orderType
                }
              })) : (this.$router.go(-1))
            }
          }
          this.loading = false
        }).catch(err => {
          console.log(err)
          this.loading = false
        })
      })
    },
    // Tip 
    handleListEdit(item) {
      // Tip 
      const params = {
        entity: {
          id: item.id, // ID
          deliveryNumber: item.deliveryNumber || 0 // 
        }
      }
      // Tip 
      return new Promise((resolve, reject) => {
        editBottomListApi(params).then(res => {
          const { code } = res
          if (!code) {
            // this.$notify.success({
            //   title: '',
            //   message: ``
            // })
            // this.listInit()
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // Tip 
    init() {
      this.listInit()
    },
    // Tip 
    async confirmCallBack() {
      this.tableComKey++
      await this.listInit()
      const i = this.tableData.findIndex(c => c.id === this.dialogForm.id)
      if (i !== -1) {
        this.dialogForm = this.tableData[i]
      }
    },
    // Tip 
    listInit() {
      this.enabled = false // 
      this.tableLoading = true
      // Tip 
      const params = {
        page: {
          ...this.pageInfo,
          ...this.params.view !== 'add' && { size: 1000 }
        },
        ...this.query,
        orderId: ['modify', 'detail'].includes(this.params.view) ? null : this.params.orderId, // id
        deliveryReceiptId: this.params.view === 'add' ? null : this.params.id // ID
      }
      return new Promise((resolve, reject) => {
        getBottomListApi({
          ...params,
          ...this.params.view === 'add' && { ids: [...this.selected] }
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.enabled = true // 
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            // 
            const lo = this.columnList.findIndex(item => !item.prop)
            this.columnList.splice(lo, 1, {
              module: 'word',
              prop: '',
              label: '',
              align: 'center',
              headerAlign: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: (c) => {
                return (c.orderNumber - c.deliveredNumber).toFixed(3)
              }
            })
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list.map(i => {
              return {
                ...i,
                ...this.params.view === 'add' && {
                  deliveryNumber: this.selected.has(i.id) ? this.selectedRows.find(c => c.id === i.id).deliveryNumber : i.orderNumber - i.deliveredNumber
                }
              }
            })
            if (this.params.view !== 'add') { // Tip 
              this.tableSelection = this.tableData.filter(item => item.orderType === 1)
            } else { // Tip 
              this.tableSelection = this.tableData.filter(item => item.selected)
              console.log('---', [...this.selected])
            }
          }
          this.tableLoading = false
          resolve(res)
        }).catch(err => {
          this.tableLoading = false
          reject(err)
        })
      })
    },
    // Tip 
    handleDelete(item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        delBottomListApi({
          entity: {
            id: item.id
          }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.listInit()
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    // Tip 
    handleAddMaterial(type /**  */) {
      if (type === '3') { // 
        this.selectInfo.visible = true
      } else { // 
        this.orderSelectInfo.visible = true
      }
    },
    // Tip 
    handleRowClick({ row, column, cell, event }) {
      this.currentRow = row
      if (column.label === '') {
        return false
      }
    },
    // Tip 
    onSelectConfirm(list) {
      if (!list.length) return
      let dmList = []
      if (this.form.orderType === '3') { // 
        dmList = list.map(i => {
          return {
            ...i,
            deliveryReceiptId: this.form.id,
            materialId: i.id,
            orderMaterialId: null,
            orderNumber: 0,
            deliveryNumber: i.deliveryNumber
          }
        })
      } else { // 
        dmList = list.map(i => {
          return {
            ...i,
            deliveryReceiptId: this.form.id,
            materialId: i.materialId,
            orderMaterialId: i.id,
            orderNumber: i.orderNum,
            deliveryNumber: i.deliveryNumber
          }
        })
      }
      // 
      addBottomListApi({
        dmList
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$notify.success({
            title: '',
            message: ``
          })
          this.listInit()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // Tip
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // Tip 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // Tip 
    handleAllGenerate() {
      return new Promise((resolve, reject) => {
        allGenerateApi({ deliveryReceiptId: this.params.id })
          .then(res => {
            this.$message({
              message: '',
              type: 'success'
            })
          })
          .catch(err => {
            console.log('err', err)
          })
      })
    },
    // Tip 
    handleAllPrint() {
      this.getDataAndPrint(allPrintApi, { deliveryReceiptId: this.params.id })
        .then(res => {
          this.listInit()
        })
    },
    // Tip 
    beforeClose() {
      this.$store.dispatch('tagsView/delView', this.$route)
      // -
      if (this.$route.params.routeName === 'InStorageDeliveryModify') {
        this.$router.push({
          name: 'InStoragePurchaseDelivery',
          params: {
            id: this.$route.params.orderId, // id
            type: this.$route.params.type // 
          }
        })
        return
      }
      let routeName = ''
      // Tip 
      if (this.params.view === 'modify' && this.params.routeName === 'InStorageDeliveryAdd') {
        // const arr = ['', '', '', '']
        const arr = ['InStoragePurchase', 'InStorageAssist', 'InStorageConsign', 'InStorageFinish']
        routeName = arr[this.form.orderType - 1]
        this.$router.push({
          name: routeName
        })
        return
      }
      routeName = this.params.routeName
      routeName ? (this.$router.push({
        name: routeName,
        params: this.$route.query.extraInfo || {}
      })) : (this.$router.go(-1))
    },
    // Tip ()
    handleBatchPrintBarcode() {
      const args = this.tableSelection.map(i => ({ id: i.id, deliveryNumber: i.deliveryNumber, number: i.number }))
      if (!args.length) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return
      }
      this.getDataAndPrint(materialPrintApi, {
        dmList: args
      })
    },
    // Tip ()
    handlePrintBarcode(item) {
      this.getDataAndPrint(materialPrintApi, {
        dmList: [{ id: item.id, deliveryNumber: item.deliveryNumber, number: item.number }]
      })
    },
    // Tip 
    handleGenerateBarcode(item) {
      this.handleListEdit(item)
        .then(() => {
          this.dialogForm = { ...item }
          this.editType = 'modify'
          this.dialogVisible = true
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // Tip 
    handleViewBarcode(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    // Tip 
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]

      if (!this.tableSelection.length) {
        this.tableData.forEach(item => {
          if (this.selected.has(item.id)) {
            this.selected.delete(item.id)
            this.selectedRows = this.selectedRows.filter(c => c.id !== item.id)
          }
        })
      } else {
        this.tableSelection.forEach(item => {
          if (!this.selected.has(item.id)) {
            this.selected.add(item.id)
            this.selectedRows.push(item)
          }
        })
      }
      console.log('__---', [...this.selected])
    },
    paginationStatusChange() {
      this.listInit()
    },
    // Tip 
    handleCellStyle({ row }) {
      const cellStyle = {
        backgroundColor: row.barcodeNumber > 0 ? '#67C23A' : ''
        // backgroundColor: row === this.currentRow ? '#409EFF' : ''
      }
      return cellStyle
    },
    // Tip 
    handleSetTable() {
      this.$refs[this.refTableName].sortDialogVisible = true
    },
    // Tip 
    selectable(row, index) {
      if (this.params.view === 'add') {
        return row.orderNumber - row.deliveredNumber !== 0
      }
      return true
    },
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehouseSapPullListApi().then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    getDynamicRow(item) {
      if (this.selected.has(item.id)) {
        this.selected.delete(item.id)
        this.selectedRows = this.selectedRows.filter(c => c.id !== item.id)
      } else {
        this.selected.add(item.id)
        this.selectedRows.push(item)
      }
      console.log('__---', [...this.selected])
    },
    // Tip 
    handleInWarehouse(item) {
      this.$router.push({
        name: 'InStorageInfoAdd',
        params: {
          id: this.params.id // id
        }
      })
    },
    handleAddMaterial1() {
      this.visible = true
    },
    handleChooseConfirm(list = []) {
      // list.forEach(c => {
      //   this.tableData.push({ ...c })
      // })
      this.init()
    }
  }
}
</script>

